  display: block;
  background-color: #f9fbfd;
  margin: 0 auto;
  position: relative;
  margin-right: 15px;
  margin-top: 15px; }
  .burger-button__cross::after {
    content: "";
    width: 100%;
    height: 2px;
    position: absolute;
    background-color: #f9fbfd;
    left: 0;
    bottom: -6px;
    transition: bottom 300ms 300ms, transform 300ms cubic-bezier(0.23, 1, 0.32, 1); }
  .burger-button__cross::before {
    content: "";
    width: 100%;
    height: 2px;
    position: absolute;
    background-color: #f9fbfd;
    left: 0;
    top: -6px;
    transition: top 300ms 300ms, transform 300ms cubic-bezier(0.23, 1, 0.32, 1); }

.burger-button__cross--active {
  background-color: transparent; }
  .burger-button__cross--active::after {
    bottom: -1px;
    transform: rotate(45deg);
    transition: bottom 300ms, transform 300ms 300ms cubic-bezier(0.68, -0.55, 0.27, 1.55);
    background-color: #011c40; }
  .burger-button__cross--active::before {
    top: 0;
    transform: rotate(-45deg);
    transition: top 300ms, transform 300ms 300ms cubic-bezier(0.68, -0.55, 0.27, 1.55);
    background-color: #011c40; }

